.body {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75); }

.h1 {
  font-family: Quicksand;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 30px;
  color: #1F2041; }

.h2 {
  font-family: Quicksand;
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 24px;
  color: #1F2041; }

.h3 {
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #1F2041; }

.text-field {
  width: 100%;
  max-width: 320px;
  height: 44px;
  background: #FFFFFF;
  box-sizing: border-box;
  border-radius: 4px;
  outline: none;
  border: 1px solid rgba(31, 32, 65, 0.25);
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  padding-left: 15px; }
  .text-field:focus, .text-field:hover {
    border: 1px solid rgba(31, 32, 65, 0.5); }

.dropdown-option {
  width: auto;
  max-width: 318px;
  height: 44px;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .dropdown-option__text {
    display: inline-block;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    text-transform: uppercase;
    color: #1F2041;
    justify-content: center;
    align-items: center;
    padding-left: 15px; }
  .dropdown-option__button-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 92px;
    width: 100%;
    padding-right: 7px; }
  .dropdown-option__circle {
    display: flex;
    border: 1px solid rgba(31, 32, 65, 0.5);
    box-sizing: border-box;
    width: 30px;
    height: 30px;
    border-radius: 15px;
    font-weight: normal;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 18px;
    color: rgba(31, 32, 65, 0.5);
    font-family: 'Montserrat'; }
  .dropdown-option__number {
    display: flex;
    text-align: center;
    vertical-align: middle;
    justify-content: center;
    align-items: center; }

.dropdown {
  display: inline-block;
  position: relative;
  width: 100%;
  max-width: 318px;
  background: #FFFFFF;
  margin-top: 5px;
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 4px; }
  .dropdown__select {
    box-sizing: border-box;
    font-family: Montserrat;
    position: relative;
    height: 44px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: rgba(31, 32, 65, 0.75);
    outline: none;
    padding: 8px 15px; }
  .dropdown__arrow {
    display: flex;
    position: absolute;
    width: 44px;
    height: 44px;
    right: 0;
    vertical-align: middle;
    justify-content: center;
    align-items: center; }
  .dropdown__options {
    display: none; }
  .dropdown__options:hover {
    display: block; }
  .dropdown:focus .dropdown__options {
    display: block; }

.material-icons {
  display: inline-block;
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.5);
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  user-select: none; }
